.control-panel .range__button:before {
  content: '';
  display: inline-block;
  height: 40px;
  position: absolute;
  bottom: -5px;
  left: 30px;
  right: 30px;
  z-index: -1;
  border-radius: 30em;
  filter: blur(20px) brightness(0.95);
  transform-style: preserve-3d;
  transition: all 0.3s ease-out;
}

.control-panel .range__button:before:hover {
  bottom: -7px;
  opacity: 1;
  filter: blur(20px);
  background: linear-gradient(2deg, #C12F2E, #f9ad40, #C12F2E, #f9ad40);
}

